import React from 'react'

function Buttons(props) {

    const propsObj = props;

    return (
        <div id="buttons">
            <button
                id="clear"
                onClick={propsObj.initialize}
                value="AC"
            >
                AC
            </button>
            <button
                id="divide"
                onClick={propsObj.operators}
                value="/"
            >
                /
            </button>
            <button
                id="multiply"
                onClick={propsObj.operators}
                value="x"
            >
                x
            </button>
            <button id="seven" onClick={propsObj.numbers} value="7">
                7
            </button>
            <button id="eight" onClick={propsObj.numbers} value="8">
                8
            </button>
            <button id="nine" onClick={propsObj.numbers} value="9">
                9
            </button>
            <button
                id="subtract"
                onClick={propsObj.operators}
                value="-"
            >
                -
            </button>
            <button id="four" onClick={propsObj.numbers} value="4">
                4
            </button>
            <button id="five" onClick={propsObj.numbers} value="5">
                5
            </button>
            <button id="six" onClick={propsObj.numbers} value="6">
                6
            </button>
            <button
                id="add"
                onClick={propsObj.operators}
                value="+"
            >
                +
            </button>
            <button id="one" onClick={propsObj.numbers} value="1">
                1
            </button>
            <button id="two" onClick={propsObj.numbers} value="2">
                2
            </button>
            <button id="three" onClick={propsObj.numbers} value="3">
                3
            </button>
            <button
                id="zero"
                onClick={propsObj.numbers}
                value="0"
            >
                0
            </button>
            <button id="decimal" onClick={propsObj.decimal} value=".">
                .
            </button>
            <button
                id="equals"
                onClick={propsObj.evaluate}
                value="="
            >
                =
            </button>
        </div>
    )
}

export default Buttons